import React, { useState } from 'react'

const Main = () => {
  
  const [nftCount, setNftCount] = useState(1)

  const handleDecrement = () => {
    if (nftCount > 1) {
      setNftCount(nftCount - 1)
    }
    
  }

  const handleIncrement = () => {
    if (nftCount < 50) {
      setNftCount(nftCount + 1)
    }
  }


  
  return (
    //scard in the center of the page
    <div className="container">
      <div className='test-card'>
        <div className="test-card-title">
          <div className="test-logo">Logo</div>
          <div className="test-subtitle">
            <span>Giving 600 ETH Back</span>
          </div>
        </div>

        <div className="test-subcard-0">
          <div className="test-total">
          <div className="test-total-title">Total Minted:</div>
          <div className="test-total-number">0/3,333</div>
          </div>
          
        </div>

        <div className="test-subcard-1">
          <div className="test-prices">
            <div className="test-price-1"><span className="color">Phase 1 Price:</span> 0.133 ETH</div>
            <div className="test-price-2"><span className="color">Phase 2 Price:</span> 0.166 ETH</div>
            <div className="test-price-3"><span className="color">Phase 3 Price:</span> 0.199 ETH</div>
          </div>
        </div>
        
        <div className="test-subcard-2">
          <div className="number">{nftCount}</div>
          {/* + - reset buttons */}
          
          <div className="test-card-buttons" >
            <div className="test-button" onClick={() => handleIncrement()}>+</div>
            <div className="test-button" onClick={() => handleDecrement()}>-</div>
            <div className="test-max-button" onClick={() => setNftCount(50)}>Max</div>
            <div className="test-reset-button" onClick={() => setNftCount(1)}>Reset</div>
          </div>
          <div className="mint-button" onClick={() => alert(`I will mint ${nftCount} NFTs`)}>Mint</div>
        </div>

        <div className="test-footer">
          <span>Reveals after every 500 mints</span>
        </div>
        
      </div>
    </div>
 

  )
}

export default Main